
.com-contain {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row;
          flex-flow: row;
  margin: 5px 5px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 16px;
  overflow: hidden;
}
.com-contain .com-menu {
    /*float: left;*/
    font-family: "Microsoft YaHei";
    margin-right: 5px;
    border: 1px solid #cdcdcd;
    padding: 10px 0;
    border-radius: 10px;
    width: 160px;
}
.com-contain .com-menu li {
      position: relative;
      list-style: none;
      line-height: 40px;
      overflow: hidden;
}
.com-contain .com-menu a {
      text-decoration: none;
      display: block;
      padding: 0 10px;
      color: #5e5e5e;
}
.com-contain .com-menu a.router-link-active {
        background: #d5d5d5;
        color: #5e5e5e !important;
}
.com-contain .com-view {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: auto;
}
.issue-point {
  position: absolute;
  right: 2px;
  top: 15px;
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background: rgba(34, 172, 56, 0.8);
}
.fade-enter-active, .fade-leave-active {
  transition: -webkit-transform .5s ease-in-out;
  transition: transform .5s ease-in-out;
  transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
  opacity: 0.4;
  width: 300px;
  height: 300px;
  background: #000;
  border: 80px solid #444;
  box-shadow: 0 0 0 80px #000;
  -webkit-transform: scale(0.4);
          transform: scale(0.4);
}
.fade-enter, .fade-leave-active {
  opacity: 0;
  -webkit-transform: scale(0.2);
          transform: scale(0.2);
}
.circle {
  position: absolute;
  border-radius: 200px;
  box-sizing: border-box;
  pointer-events: none;
}



