
@-webkit-keyframes barberpole {
from {
    background-position: 0% 0%;
}
to {
    background-position: 600px 0%;
}
}
@keyframes barberpole {
from {
    background-position: 0% 0%;
}
to {
    background-position: 600px 0%;
}
}
.step {
  -webkit-box-flex: 1;
  text-align: center;
}
.step-active {
  /*background-image: linear-gradient(top,#ababab,#eee 50%,#ababab 100%);*/
  background: #999;
}
.step-first {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
.step-last {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.step-title {
  position: relative;
  top: 22px;
  display: block;
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: #ccc;
  font-weight: bold;
}
.title-active {
  color: #788184;
}
.step-wrap-annular {
  display: inline-block;
  position: relative;
  bottom: 68px;
  width: 40px;
  height: 40px;
  background: #ddd;
  border-radius: 20px;
}
.step-wrap-annular .step-inner-annular {
    display: block;
    width: 30px;
    height: 30px;
    position: relative;
    top: 5px;
    left: 5px;
    border-radius: 15px;
    background: #fff;
    color: #ddd;
    line-height: 30px;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
}
.step-active .step-wrap-annular {
  background: #bababa;
}
.step-active .step-wrap-annular .step-inner-annular {
    background: #fff;
    color: #f98451;
}
.light {
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 60px;
  height: 20px;
  background-image: linear-gradient(0deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0));
  -webkit-transform: skewx(-25deg);
          transform: skewx(-25deg);
  -webkit-animation: lightmove ease-in-out 1500ms infinite;
          animation: lightmove ease-in-out 1500ms infinite;
}
@-webkit-keyframes lightmove {
from {
    left: 0;
}
to {
    left: 100%;
}
}
@keyframes lightmove {
from {
    left: 0;
}
to {
    left: 100%;
}
}
.current-title {
  color: #aba082;
  /*color: #f98451;*/
}



