
@-webkit-keyframes bg-color-animation {
from {
    background: #fff;
}
to {
    background: #d5d5d5;
}
}
@keyframes bg-color-animation {
from {
    background: #fff;
}
to {
    background: #d5d5d5;
}
}
.com-header {
  position: relative;
  height: 70px;
  padding-top: 10px;
  background: #fff;
  font-size: 32px;
  line-height: 80px;
  text-align: center;
  -webkit-animation: bg-color-animation 7s linear;
          animation: bg-color-animation 7s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.com-header .bg {
    position: absolute;
    left: 10px;
    top: 0;
    width: 80px;
    height: 80px;
    background: url("../../src/assets/logo.png") no-repeat;
    background-size: 100%;
}
.com-header path {
    color: red;
}



