
.svg-pos {
  position: relative;
}
@-webkit-keyframes progressAnimation {
from {
    stroke-dashoffset: 100px;
}
to {
    stroke-dashoffset: 0;
}
}
@keyframes progressAnimation {
from {
    stroke-dashoffset: 100px;
}
to {
    stroke-dashoffset: 0;
}
}
circle {
  -webkit-animation: progressAnimation 600ms linear 1;
          animation: progressAnimation 600ms linear 1;
}
.percentColor {
  position: absolute;
  width: 100%;
  height: 30px;
  top: 50%;
  text-align: center;
  margin-top: -15px;
  line-height: 30px;
  font-weight: bold;
  color: #515151;
}



