
@-webkit-keyframes bg-color-animation {
from {
    background: #fff;
}
to {
    background: #d5d5d5;
}
}
@keyframes bg-color-animation {
from {
    background: #fff;
}
to {
    background: #d5d5d5;
}
}
.com-footer {
  height: 80px;
  font-size: 32px;
  line-height: 80px;
  text-align: center;
  -webkit-animation: bg-color-animation 7s linear;
          animation: bg-color-animation 7s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}



