
@charset "UTF-8";
.shape[data-v-2c4d5e10] {
  width: 100px;
  height: 100px;
  background: #22ac38;
  border: 10px solid red;
}

/*6种过渡状态*/
.a-enter[data-v-2c4d5e10] {
  opacity: 0;
  background: red;
  -webkit-transform: scale(0.2);
          transform: scale(0.2);
}
.a-enter-to[data-v-2c4d5e10] {
  opacity: 1;
  /*background: rgba(34, 172, 56, 1);*/
}
.a-enter-active[data-v-2c4d5e10] {
  transition: all 3s;
}

/*为啥leave的时候不是从蓝色开始过渡的呢？leave好像没有生效*/
/*大概知道了，
leave-to的状态部分从元素显示之后达到的本身的状态值开始，
比如背景色，
不能任性地越变，比如translateX也不能越变，*/
.a-leave[data-v-2c4d5e10] {
  opacity: 1;
  background: red !important;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  /*border: 20px solid black;*/
}
.a-leave-to[data-v-2c4d5e10] {
  opacity: 0;
  -webkit-transform: rotate(-180deg) translateX(-200px);
          transform: rotate(-180deg) translateX(-200px);
  border-radius: 100px;
  background: orange;
  border: 10px solid black;
}
.a-leave-active[data-v-2c4d5e10] {
  transition: all 3s;
}

/*❤形状样式*/
.heart[data-v-2c4d5e10] {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  margin: 30px 60px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  /*background: rgba(34, 172, 56, 1);*/
}
.heart[data-v-2c4d5e10]:before, .heart[data-v-2c4d5e10]:after {
    position: absolute;
    display: block;
    content: '';
    width: 80px;
    height: 80px;
    border-radius: 100%;
    /*background: rgba(34, 172, 56, 1);*/
}
.heart[data-v-2c4d5e10]:before {
    left: -50%;
    top: 0;
}
.heart[data-v-2c4d5e10]:after {
    right: 0;
    top: -50%;
}



