
@charset "UTF-8";
.com-list-block {
  /*width: 292px;*/
  /*height: 370px;*/
  float: left;
  position: relative;
  overflow: hidden;
  background: #fff;
  margin-right: 1px;
  /*心*/
  /*图片切换的交互适合放在外面*/
}
.com-list-block .heart {
    display: inline-block;
    position: absolute;
    right: 5px;
    top: 10px;
    width: 12px;
    height: 12px;
    margin: 0 6px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    background: #ccc;
    transition: all .3s;
    cursor: pointer;
    opacity: 0;
    z-index: 3;
    /*激活的时候加上这个class*/
}
.com-list-block .heart:before, .com-list-block .heart:after {
      position: absolute;
      display: block;
      content: '';
      width: 12px;
      height: 12px;
      border-radius: 100%;
      background: #ccc;
      transition: background-color .3s;
}
.com-list-block .heart:before {
      left: -50%;
      top: 0;
}
.com-list-block .heart:after {
      right: 0;
      top: -50%;
}
.com-list-block .heart:hover {
      background: red;
      /*控制一下逻辑，心形hover的时候保证图片的上下位置*/
}
.com-list-block .heart:hover:before, .com-list-block .heart:hover:after {
        background: red;
}
.com-list-block .heart:hover ~ .img-group a.img-up {
        opacity: 0;
}
.com-list-block .heart:hover ~ .img-group a.img-down {
        opacity: 1;
}
.com-list-block .heart.active {
      background: red;
}
.com-list-block .heart.active:before, .com-list-block .heart.active:after {
        background: red;
}
.com-list-block:hover .add-cart-btn {
    /*bottom: 0;*/
    /*opacity: 1;*/
    display: block;
    -webkit-animation: flipInX .5s;
            animation: flipInX .5s;
}
.com-list-block:hover .heart {
    opacity: 1;
}
.com-list-block:hover .product-name {
    opacity: 0;
}
.com-list-block .add-cart-btn {
    width: 100%;
    height: 50px;
    line-height: 50px;
    color: #fff;
    text-align: center;
    background: #20c3b6;
    border: none;
    position: absolute;
    display: none;
    bottom: 0;
    /*<!--bottom: -50px;-->*/
    /*<!--opacity: 0.3;-->*/
    /*<!--transition: opacity .3s ease-in-out,bottom .3s ease-in-out;-->*/
    cursor: pointer;
    z-index: 1;
    transition: background .3s;
}
.com-list-block .add-cart-btn:hover {
      background: #09b1a4;
}
.com-list-block .price {
    text-align: center;
    height: 26px;
    line-height: 26px;
    font-size: 14px;
    font-weight: 600;
    position: absolute;
    bottom: 50px;
    width: 100%;
}
.com-list-block .price .price-new {
      color: #000;
}
.com-list-block .price .price-old {
      text-decoration: line-through;
      color: #999;
      margin-left: 6px;
}
.com-list-block .product-name {
    font-size: 12px;
    font-style: italic;
    letter-spacing: 1px;
    color: #888;
    text-align: center;
    position: absolute;
    width: 100%;
    bottom: 76px;
    opacity: 1;
    transition: opacity .5s;
}
.com-list-block .tag-group {
    width: 100%;
    height: 48px;
    position: absolute;
    bottom: 0;
    font-size: 12px;
    text-align: center;
    font-family: 'OpenSans-Semibold';
}
.com-list-block .tag-group .green-tag, .com-list-block .tag-group .orange-tag, .com-list-block .tag-group .red-tag {
      display: inline-block;
      height: 16px;
      padding: 0 5px;
      clear: both;
      margin-bottom: 5px;
}
.com-list-block .tag-group .green-tag {
      border: 1px solid #48d46f;
      color: #48d46f;
}
.com-list-block .tag-group .orange-tag {
      border: 1px solid #ff7e00;
      color: #ff7e00;
}
.com-list-block .tag-group .red-tag {
      border: 1px solid #f4354a;
      color: #f4354a;
}
.com-list-block:hover .img-group a.img-up {
    opacity: 0;
}
.com-list-block:hover .img-group a.img-down {
    opacity: 1;
}
.com-list-block .img-group {
    width: 100%;
    height: 240px;
    /*&:hover{*/
    /*a{*/
    /*&.img-up{ opacity: 0}*/
    /*&.img-down{ opacity: 1}*/
    /*}*/
    /*}*/
}
.com-list-block .img-group a {
      width: 100%;
      height: 240px;
      display: block;
      position: absolute;
      left: 0;
      opacity: 1;
      /*background: url("../../../assets/imgs/loading.gif") no-repeat center top;*/
      transition: opacity .3s;
}
.com-list-block .img-group a img {
        width: 100%;
        height: 240px !important;
}
.com-list-block .img-group a.img-up {
        z-index: 2;
        opacity: 1;
}
.com-list-block .img-group a.img-down {
        z-index: 1;
        opacity: 0;
}
.com-list-block .sale-tag {
    width: 100%;
    position: absolute;
    top: 18px;
    left: -40%;
    text-align: center;
    height: 16px;
    background: #f4354a;
    color: #fff;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    z-index: 3;
    font-size: 12px;
}



