
.listdemo08-list {
  width: 807px;
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  margin: 0px 0 10px 0;
}
.listdemo08-list li {
    padding: 5px 10px;
    font-size: 14px;
    list-style: none;
    line-height: 28px;
    border-bottom: 1px dashed #ccc;
}
.listdemo08-list li.more {
      border-bottom: none;
}
.listdemo08-list li a {
      text-decoration: none;
      color: #22ac38;
}
.listdemo08-list li .exp {
      font-weight: bold;
}



